body {
  background-image: radial-gradient(#81bcff, #303391);
  background-size: 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  text-align: center;
}

#rotate-words {
  max-width: 400px;
  margin: auto;
  padding: 5% 0;
  font-size: 4em;
  text-transform: uppercase;
  font-family: "Oswald", sans-serif;
  min-height: 400px;
  color: #d4af37;
}

#rotate-words span {
  display: block;
  height: 100px;
  font-size: 1em;
  text-transform: lowercase;
  opacity: 0.8;
  font-family: "Rubik", sans-serif;
}

#rotate-words div {
  position: absolute;
  opacity: 0;
  overflow: hidden;
  left: 10vw;
  width: 80vw;
  line-height: 1.2em;
  animation: rotate-word 36s linear infinite 0s;
}

@keyframes rotate-word {
  0% {
    opacity: 0;
    transform: translateX(0);
    -webkit-filter: blur(10px);
            filter: blur(10px);
    transform: scale(1.2);
  }
  3% {
    opacity: 1;
    transform: translateX(0);
    -webkit-filter: blur(0px);
            filter: blur(0px);
    transform: scale(0.9);
  }
  12% {
    opacity: 1;
    transform: translateX(0);
    -webkit-filter: blur(0px);
            filter: blur(0px);
    transform: scale(1);
  }
  16% {
    opacity: 0;
    transform: translateX(0);
    -webkit-filter: blur(10px);
            filter: blur(10px);
    transform: scale(1.2);
  }
  80% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

#rotate-words div:nth-child(2) {
  animation-delay: 4s;
}
#rotate-words div:nth-child(3) {
  animation-delay: 8s;
}
#rotate-words div:nth-child(4) {
  animation-delay: 12s;
}
#rotate-words div:nth-child(5) {
  animation-delay: 16s;
}
#rotate-words div:nth-child(6) {
  animation-delay: 20s;
}
#rotate-words div:nth-child(7) {
  animation-delay: 24s;
}
#rotate-words div:nth-child(8) {
  animation-delay: 28s;
}
#rotate-words div:nth-child(9) {
  animation-delay: 32s;
}

@keyframes author {
  0% {
    opacity: 0;
    transform: translateY(100px);
    -webkit-filter: blur(10px);
            filter: blur(10px);
    transform: scaleY(2);
  }
  20% {
    opacity: 0;
    transform: translateY(200px);
    -webkit-filter: blur(10px);
            filter: blur(10px);
    transform: scaleY(2);
  }
  30% {
    opacity: 1;
    transform: translateY(0);
    -webkit-filter: blur(0px);
            filter: blur(0px);
    transform: scaleY(1);
  }
  90% {
    opacity: 1;
    transform: translateY(0);
    -webkit-filter: blur(0px);
            filter: blur(0px);
    transform: scaleY(0.9);
  }
  100% {
    opacity: 0;
    transform: translateY(0);
    -webkit-filter: blur(10px);
            filter: blur(10px);
    transform: scale(2);
  }
}

